
import Component from "vue-class-component";
import Vue from "vue";
import firebase from "firebase/app";
import "firebase/auth";
import db from "../firebase";
import { uid } from "uid";
import { ruleEmail } from "@/helpers";
import Loading from "../components/loading/Loading.vue";

@Component({
  components: {
    Loading,
  },
})
export default class RegisterPage extends Vue {
  public password: string = "";
  public confirmPassword: string = "";
  public name: string = "";
  public phoneNumber: string = "";
  public address: string = "";
  public error: string = "";
  public errorEmail: string = "";
  public errorPassword: string = "";
  public errorConfirmPassword: string = "";
  public isVisible: boolean = false;
  public isVisibleConfirm: boolean = false;
  public loading: boolean = false;
  public uuid: string = uid();
  public handleToggle() {
    this.isVisible = !this.isVisible;
  }
  public handleToggleConfirm() {
    this.isVisibleConfirm = !this.isVisibleConfirm;
  }
  public get checkEmail() {
    return this.$store.getters.checkEmail;
  }
  public signUp() {
    if (!ruleEmail(this.$store.state.email)) {
      this.errorEmail = "Invalid email!!!";
    } else {
      this.errorEmail = "";
    }
    if (this.password.length < 6) {
      this.errorPassword = "Password of at least 6 characters!!!";
    } else {
      this.errorPassword = "";
    }
    if (this.password != this.confirmPassword) {
      this.errorConfirmPassword = "Password mismatch!!!";
    } else {
      this.errorConfirmPassword = "";
    }
    if (
      ruleEmail(this.$store.state.email) &&
      this.password.length >= 6 &&
      this.password == this.confirmPassword
    ) {
      if (!this.checkEmail) {
        this.loading = true;
        firebase
          .auth()
          .createUserWithEmailAndPassword(
            this.$store.state.email,
            this.password
          )
          .then(() => {
            this.loading = false;
            db.ref("Info" + `/${this.uuid}`).set({
              email: this.$store.state.email,
              name: this.name,
              phone: this.phoneNumber,
              address: this.address,
              uid: this.uuid,
            });
            this.$router.push({ name: "Home" });
          })
          .catch((err) => {
            this.loading = false;
            this.error = "Register failed!!!";
            console.log(err.message);
          });
      } else {
        this.error = "Email already exists!!!";
      }
    }
  }

  public handleLogin() {
    this.$router.push({ name: "Login" });
  }
}
